@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&family=Stylish&display=swap);
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}

.main{display:flex;flex-direction:column;align-items:center;justify-content:center;height:100vh;width:100vw;padding:0px 30px;max-width:450px}.main *{font-family:"Stylish", sans-serif}.main .login-title{margin-bottom:40px;font-weight:700;font-size:2em;font-style:italic;color:#444}.main .signin-form{display:flex;flex-direction:column;align-items:center;width:100%}.main .signin-form .signin-form__email,.main .signin-form .signin-form__password{margin-bottom:10px;width:100%}.main .signin-form .signin-form__submit{font-size:1.2em;width:100%;background-color:#1976d1;color:white;margin-top:10px}.main .social-login{display:flex;flex-direction:column;margin-top:20px;width:100%}.main .social-login button{color:white;margin:5px 0px}.main .social-login .social-login__google{background-color:#ea232c}.main .social-login .social-login__github{background-color:#444}.main .toggle-account{display:inline-block;width:100%;margin:10px;color:#1976d1;text-align:right}.main .toggle-account:hover{cursor:pointer}.footer{margin:30px 0;padding-top:15px;border-top:3px solid rgba(0,0,0,0.1);width:100%;display:flex;flex-direction:column;align-items:center}.footer .footer__icon{color:rgba(0,0,0,0.2);font-size:30px;margin-bottom:10px}.footer .footer__text{color:rgba(0,0,0,0.2);font-weight:800;text-transform:uppercase}*{box-sizing:border-box;font-family:"Source Sans Pro", sans-serif}body{background-color:white;display:flex;justify-content:center}.main button,.main .signin-form .signin-form__submit{border:none;border-radius:10px;padding:10px;width:100%;font-weight:400;font-size:1.1em}.main input,.main .signin-form .signin-form__email,.main .signin-form .signin-form__password{border:1px solid #a7a7a7;border-radius:5px;padding:10px;font-size:1em}.main input:hover{border:1px solid #444}.main input:focus{outline:0;border:2px solid #3498db}

